









import { Component, Prop, Vue, Emit } from 'vue-property-decorator';
import { Realtime, TextMessage, Event } from 'leancloud-realtime';
const {
    TypedMessagesPlugin,
    ImageMessage,
} = require('leancloud-realtime-plugin-typed-messages');

@Component
export default class Registered extends Vue {
    private userName = '';
    private password = '';
    private email = '';

    @Emit()
    private registered() {
        if (this.userName === '' || this.password === '' || this.email === '') {
            return;
        }
        // 新建 AVUser 对象实例
        const user = new this.$store.state.AV.User();
        // 设置用户名
        user.setUsername(this.userName);
        // 设置密码
        user.setPassword(this.password);
        // 设置邮箱
        user.setEmail(this.email);
        const avatar = this.upAvatar;
        user.set('avatar', avatar);
        user.signUp().then(
            (user: any) => {
                this.$store.state.realtime
                    .createIMClient(user)
                    .then((user: any) => {
                        this.$store.commit('initUser', user);
                    });
            },
            function(error: any) {
                console.log(error)
            },
        );
    }
    @Emit()
    private upAvatar() {
        const fileUploadControl: any = document.querySelector('sd');
        if (fileUploadControl.files.length > 0) {
            const localFile = fileUploadControl.files[0];
            const name = 'avatar.jpg';

            return new this.$store.state.AV.File(name, localFile);
        }
    }
}
